.section-third {
    overflow: hidden;

    &__container {
        position: relative;
        max-width: 1210px;
        min-height: 1110px;
        display: block;
        margin: 4.6875rem auto 6.25rem auto;
    }

    &__title {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 2rem;
        line-height: 2.625rem;
    }

    &__title-two {
        margin: 2.9375rem 0 0 1.3125rem;
        color: var(--secondary-color);
    }

    &__content-box {
        display: flex;
        justify-content: center;
        min-height: 771px;
    }

    &__footer {
        width: 100%;
        min-height: 296px;
        background-color: var(--bg-color);
        border-top: 1px solid transparent;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }

    &__footer-circle {
        width: 80px;
        height: 80px;
        border-radius: 2px solid var(--secondary-color);
    } 

    &__list {
        margin: 1.9375rem 0 0 1.3125rem;
        max-width: 1045px;
    }
}

.content {
    position: relative;
    margin-top: 4.875rem;
    margin-left: 3.125rem;
    
    &:first-child {
        border-right: 1px solid var(--primary-color);
    }

    &__check {
        left: 1.625rem;
        top: 0;
        background-image: url("../images/pur_check.svg");
        width: 202px;
    }

    &__check-sec {
        background-image: url("../images/pur_check_big.svg");
        width: 378px;
    }

    &__list {
        margin-top: 4.4375rem;
        margin-bottom: 3.9375rem;
        max-width: 357px;
        list-style: url("../images/dot.svg");
    }

    &__list-sec {
        margin: 2.8125rem 0 0.625rem 0;
        list-style: none;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        max-width: 540px;
    }
    
    &__item {
        padding-left: 1.5rem;
        margin: 1.0625rem 0;

        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 1.25rem;
    }

    &__title {
        max-width: 191px;
    }

    &__item-sec {
        width: 244px;
        height: 38px;
        margin-bottom: 3.5625rem;
    }

    &__item-box-size {
        width: 100%;

        &:first-child {
            margin: 0;
        }
    }

    &__circle {
        width: 34px;
        height: 34px;
        border: 7px solid var(--icon-second-color);
        margin-right: 0;

        &::before {
            width: 12px;
            height: 12px;
            background-color: var(--icon-color);
        }
    }

    &__img-box {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        margin-left: 1.5rem;
    }

    &__title-box {
        margin-top: 0.625rem;
        margin: 0.625rem 0 0 1.25rem;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 1.125rem;
        max-width: 333px;
    }

    &__button {
        width: 346px;
        font-family: 'Roboto Condensed';
        font-style: normal;
        font-weight: 700;
        font-size: 1.5rem;
        line-height: 1.75rem;

        margin-top: 3.75rem;
    }

    &__img-notebook {
        max-width:100%;
    }
}

.list {
    &__footer-circle {
        min-width: 80px;
        min-height: 80px;
        border: 2px solid var(--secondary-color);
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

    }

    &__footer-item {
        display: flex;
        max-width: 496px;
        justify-content: space-between;
        align-items: center;
    }

    &__footer-box-title {
        margin-left: 1.25rem;
        color: var(--secondary-color);
        min-height: 74px;
        max-width: 347px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

    }

    &__footer-title-bold {
        font-weight: bold;
    }
}

.icon-zavod,
.icon-home {
    font-size: 1.5625rem;
    color: var(--icon-second-color);
}
