@mixin fontello($code, $position:before) {
    @include fontello-init($position);
    @include fontello-content($code, $position);
}


@mixin fontello-init($position:before) {
    &:#{$position} {
        font-family: $fontello-font-name;
        font-style: normal;
        font-weight: normal;

        text-decoration: inherit;
        width: 1em;
        text-align: center;

        font-variant: normal;
        text-transform: none;

        line-height: 1em;

        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

@mixin fontello-content($code, $position:before) {
    &:#{$position} {
        content: '#{$code}';
    }
}

@mixin rotation() {
    animation-name: rotation;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;

    @keyframes rotation {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }
}

@mixin pulsing() {
    animation: pulsing 1s infinite;

    @keyframes pulsing {
        0% {
            transform: scale(.85);
        }

        20% {
            transform: scale(1);
        }

        40% {
            transform: scale(.85);
        }

        60% {
            transform: scale(1);
        }

        80% {
            transform: scale(.85);
        }

        100% {
            transform: scale(.85);
        }
    }
}