.section-form {
    min-height: 727px;

    &__background {
        background-image: url("../images/backphoto.png");
        background-repeat: no-repeat;
        min-height: 727px;
        max-width: 1920px;
        margin: 0 auto;
        border-top: 1px solid var(--icon-color);
    }

    &__container {
        background-color: var(--secondary-color);
        max-width: 972px;
        min-height: 500px;
        margin: 8.3125rem auto 0 auto;
        border-top: 1px solid transparent;
    }

    &__box {
        position: relative;
        max-width: 705px;
        min-height: 410px;
        margin: 1.5625rem auto 4.0625rem auto;
        border-top: 1px solid transparent;
    }

    &__title {
        position: relative;
        margin: 3.0625rem 0 0 4.25rem;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 2.1875rem;
        line-height: 2.625rem;
        z-index: 800;
    }

    &__circle {
        position: relative;
        width: 364px !important;
        height: 364px !important;
        border: 72px solid var(--third-color) !important;
        top: -3.75rem;
        left: -3.75rem;

        &::before {
            width: 124px !important;
            height: 124px !important;
        }
    }

    &__box-circle {
        overflow: hidden;
        position: absolute;
        left: 0;
        top: 0;
        width: 318px;
        height: 313px;
    }

    &__btn {
        width: 211px;
    }
}

.form {
    position: relative;
    z-index: 990;
    min-height: 276px;
    margin: 2.6875rem 0 0 4.0625rem;

    &__groups-input {
        max-width: 640px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__group {
        width:306px;
        min-height: 192px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        // &:first-child {
        //     margin-right: 24px;
        // }

        &:last-child {
            justify-content: start;
        }
    }

    &__position {

        &:first-child {
            margin: 0px 0 1.5rem 0;
        }
    }

    &__item {
        position: relative;
        min-height: 48px;
    }

    &__input-text {
        width: 100%;
        border: none;
        border-bottom: 1px solid var(--primary-color);
        outline: none;
        background: none;
        padding: 0.3125rem 0;
        /* margin: 48px 0 0 0; */
        font-size: 1.2em;
        color: var(--primary-color);
        margin: 0.3125rem 0 0 0;

        &:focus {
            border-bottom: 2px solid var(--icon-color);
            
            +.form__placeholder {
                color: var(--icon-color);
            }
        }
    }

    &__placeholder {
        position: absolute;
        left: 0;
        top: -0.625rem;

        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 0.75rem;
        line-height: 1.25rem;
        color: rgba(0, 0, 0, 0.54)
    }

    &__groups-control {
        display: flex;
        justify-content: space-between;
    }

    &__text-box {
        max-width: 370px;
    }

    &__text {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 0.75rem;
        line-height: 1.125rem;
    }

    &__btn {
        background-image: url("../images/Rectangle_fet.svg");
        background-size: (211px 60px);
        background-repeat: no-repeat;
        background-color: transparent;
        height: 63px;
        width: 214px;
        border: none;
        color: var(--secondary-color);

        font-family: 'Roboto Condensed';
        font-style: normal;
        font-weight: 700;
        font-size: 1.5rem;
        line-height: 1.75rem;
    }
}
