@media screen and (max-width: 1400px) {
}

@media screen and (max-width: 1200px) {
    .header {
        &__menu-contacts {
            margin: 1.1875rem 1.1875rem 0 1.1875rem;
        }

        &__navigation {
            justify-content: space-around;
        }

        &__logo {
            font-size: 2.5rem;
        }
    }

    .list-navigation {
        width: 100%;
    }

    .list-contacts {
        max-width: 100%;
    }

    .list {
        &__link {
            font-size: 0.875rem;

        }
    }


    //*********** (Первая секция)
    .section-first {


        &__situation {
            max-width: 346px;
            margin: 3.125rem 0 0 0;
        }

        &__list {
            margin: 3.125rem 0 0 0;
        }

        &__img-girls {
            width: 100%;
        }

        &__check {
            left: 0;
            top: -0.625rem;
        }

        &__check-two {
            left: 0;
            top: 0.1875rem;
        }

        &__title {
            margin-top: 0.25rem;
            font-size: 0.8125rem;
        }

        &__circle {
            width: 90px;
            height: 90px;
            margin-right: 1.5rem;
            border: 19px solid var(--third-color);

            &::before {
                width: 21px;
                height: 21px;
            }
        }

        &__circle-num {
            width: 23px;
            height: 23px;
            font-size: 0.75rem;
        }

        &__number {
            font-size: 5.875rem;
            line-height: 8.6875rem;
        }

    }

    //Вторая секция
    .section-second {
        &__title {
            margin: 4.375rem auto 4.8125rem auto;
            font-size: 2rem;
            text-align: center;
        }
        
        &__btn {
            z-index: 100;
            margin: 0.9375rem auto 0 auto;
        }
    }

    .list-sec {
        margin: 0 auto;
        justify-content: space-around;
    }

    // *************** Третья секция 
    .section-third {
        &__content-box {
            flex-wrap: wrap;
        }

        &__title {
            text-align: center;
        }
    }

    .content {
        // margin-left: 0;
        max-width: 517px;
        margin-bottom: 1.875rem;

        &:first-child {
            border-right: 0;
        }

        &__circle {
            width: 40px;
            height: 40px;
            border: 7px solid var(--icon-second-color);
            margin-right: 0;

            &::before {
                width: 12px;
                height: 12px;
            }
        }

        &__item-sec {
            margin-bottom: 0.75rem;
        }


    }

    //****************Четвёртая секция
    .section-fourth {
        &__circle-all {
            width: 272px;
        }

        &__circle-1{
            left: -12.5625rem;
        }

        &__circle-2 {
            left: -12.875rem;
        }

        &__circle-3 {
            top: 2.375rem;
        }

        &__circle-4 {
            top: 16.4375rem;
        }

        &__text {
            max-width: 204px;
        }

        &__title {
            margin: 4.375rem 0 0 0;
        }
    }

    //**************************Пятая секция

    .section-fifth {
        &__title {
            text-align: center;
        }
    }

    //*************** Шестая секция

    .start {
        &__main-txt {
            display: block;
            text-align: center;
        }

        &__main-block {
            justify-content: center;
        }
    }

    //******** Footer секция

    .list-footer-navigation {
        flex-direction: column;
        width: 130px;
    }
}

@media screen and (max-width: 992px) {
    .header {
        &__nav-toggle-label {
            display: block;
        }

        &__menu-navigation {
            position: absolute;
            right: 0;
            // top: -14px;
            top: -20.625rem;
            width: 100%;
            z-index: 1000;
            background-color: #4e4e4e;
            // display: none;
            transition: all 0.5s ease-in;
        }

        &__navigation {
            justify-content: space-around;
        }

        
        &__nav-toggle:checked ~ .header__menu-navigation {
            // display: block;
            top: -0.875rem;
            border-bottom-right-radius: 50%;
            border-bottom-left-radius: 50%;
        }
    }

    .list-navigation .list__item{
        padding: 0.625rem;
    }

    // .header__menu-contacts{
    //     margin: 19px auto 0 auto;
    // }
    // .list {

    //     &__item {
    //         padding: 10px;
    //     }
    // }

    .list-navigation {
        margin: 0;
        width: 100%;
        flex-direction: column;
        text-align: center;
    }

    //******************** Вторая секция

    .list {
        &__section-second {
            width: 320px;
            height: 100px;
        }

        &__title-sectrion-second {
            max-width: 202px;
            font-size: 0.9375rem;
        }
    }

    .section-second {
        &__title {
            font-size: 1.25rem;
        }
    }

    .icon-section-second {
        font-size: 1.7637rem;
    }


    //************Третья секция 
    .section-third {
        &__list {
            justify-content: center
        }
    }

    .content {
        margin-left: 0;
    }

    .list {
        &__footer-item {
            margin-bottom: 1.875rem;
        }
    }

    
    //****************Четвёртая секция
    .section-fourth {
        &__circle-all {
            width: 272px;
        }

        &__circle-1 {
            left: 0.5625rem;
            flex-direction: row;
        }

        &__circle-2 {
            left: 0.25rem;
            flex-direction: row;
        }

        &__circle-3 {
            left: 7.5rem;
            flex-direction: row-reverse;
        }

        &__circle-4 {
            left: 17.5rem;
            flex-direction: row-reverse;
        }

        &__text {
            color: #fff;
            text-shadow: -0   -1px 2px #000000,
                          0   -1px 2px #000000,
                         -0    1px 2px #000000,
                          0    1px 2px #000000,
                         -1px -0   2px #000000,
                          1px -0   2px #000000,
                         -1px  0   2px #000000,
                          1px  0   2px #000000,
                         -1px -1px 2px #000000,
                          1px -1px 2px #000000,
                         -1px  1px 2px #000000,
                          1px  1px 2px #000000,
                         -1px -1px 2px #000000,
                          1px -1px 2px #000000,
                         -1px  1px 2px #000000,
                          1px  1px 2px #000000;
        }
    }
}



@media screen and (max-width: 768px) {
    .header {
        &__logo {
            font-size: 2rem;
        }
    }

    .list {
        &__box-contacts {
            width: 177px;
        }

        &__box-links {
            width: 150px;
        }

        &__number {
            font-size: 1.125rem;
        }

        &__email {
            font-size: 0.6875rem;
        }

        &__button {
            width: 150px;
        }

        &__box-address {
            width: 167px;
        }

        &__address {
            font-size: 0.6875rem;
        }
    }

    .list-contacts {

        &__btn-title {
            font-size: 0.8125rem;
        }
    }
    .icon-cursore, .icon-speeker {
        font-size: 0.9375rem;
    }

    //**********Третья секция 
    .section-third {
        &__title {
            font-size: 1.375rem;
        }

        &__title-two {
            margin: 2.9375rem 0 0 0;
        }

        &__list {
            margin: 1.9375rem 0 0 0;
        }
    }

    //*********Четвёртая секция
    .section-fourth {
        &__circle-1 {
        }
    }



    //************Пятая секция 
    .section-fifth {
        &__box {
            flex-direction: column;
            align-items: center;
        }

        &__box-logo {
            height: 9px;
        }

        &__title {
            font-size: 1.375rem;
        }

        &__check {
            display: none;
        }

        .content__title {
            font-size: 1.375rem;
        }

        .content__text {
            font-size: 0.8125rem;
        }
    }

    //******** Шестая секция

    .start {
        &__main-txt {
            font-size: 1.375rem;
        }
    }

    //******** Седьмая секция

    .section-form {
        &__title {
            font-size: 1.375rem;
            text-align: center;
            margin: 3.0625rem 0.625rem 0 0.625rem;
        }
    }

    .form {
        &__group {
            width: 281px;
            margin: 0px 1.875rem 2.5rem 1.875rem;
        }

        &__text-box {
            margin: 0 1.875rem;
        }
    }

    //******** Footer секция
    .list-contacts-footer {
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
}

@media screen and (max-width: 576px) {
    .header {

        &__menu-contacts {
            display: flex;
            justify-content: center;
        }

        &__box {
            margin: 6rem auto 0 auto;
            max-width: 282px;
        }

        &__title {
            font-size: 1.5rem;
            margin-left: 9rem;
        }

        &__aliexpress {
            width: 280.95px;
        }

        &__text {
            font-size: 1.0625rem;
        }

        &__button {
            width: 207px;
        }

        &__btn-title {
            font-size: 1.0625rem;
        }
    }

    .list-contacts {
        flex-direction: column;
        height: 120px;
    }

    //************ первая секция

    .section-first{
        &__box {
            max-width: 195px;
        }
    }

    // *************** Вторая секция 
    .section-second {
        &__btn-title {
            font-size: 1.0625rem;
        }
    }

    //********Третья секция

    .section-first {
        &__check-title {
            font-size: 0.875rem;
        }
        
    }

    .content {
        &__item {
            font-size: 0.6875rem;
        }

        &__check-sec {
            width: 300px;
            background-size: 300px 40px;
            background-repeat: no-repeat;
        }
    }

    .list {
        &__footer-title-description {
            font-size: 0.875rem;
        }
    }


    //Четвёртая секция
    .section-fourth {
        &__container {
            min-height: auto;
        }

        &__text {
            font-size: 0.75rem;
        }

        &__box {
            width: 270px;
            height: 292px;
        }

        &__circle-all {
            width: 229px;
        }

        &__circle-1 {
            top: 10.625rem;
        }

        &__circle-2 {
            top: 1.3125rem;
        }

        &__circle-3 {
            left: -1.0625rem;
            top: -1.875rem;
            flex-direction: row-reverse;
        }

        &__circle-4 {
            left: 3.5rem;
            top: 5rem;
            flex-direction: row-reverse;
        }

        &__circle {
            min-width: 32px;
            min-height: 32px;
            border: 3px solid var(--icon-second-color);

            &::before {
                min-width: 20px;
                min-height: 20px;
            }
        }

        &__circle-num {
            font-size: 0.875rem;
        }
    }

    //*************** Пятая секция
    .statistic {
        justify-content: center;

        &__nums {
            font-size: 1.75rem;
        }

        &__title {
            font-size: 0.75rem;
        }
    }

    .content {
        &__box {
            display: flex;
            flex-direction: column;
            /* justify-content: center; */
            align-items: center;
        }
    }


    .section-fifth {

        .content {

            &__title{
                max-width: 300px;
                text-align: center;
            }

            &__text {
                max-width: 300px;
            }
        }
    }

    //******** Шестая

    .start {
        &__img {
            width:40px;
            height: 40px;
        }

        &__main-block-txt {
            font-size: 0.75rem;
        }
    }

    .right-arrow {
        width: 30px;

        &::before {
            width: 30px;
        }
    }

    //******** Седьмая секция

    .form {
        margin: 2.6875rem 0 0 0;

        &__groups-input {
            justify-content: center;

        }
    }

    //footer 
    .footer {
        &__menu {
            display: none;
        }
        
        &__menu-contacts {
            margin-top: 3.5625rem;
        }
    }
}