@import "mixins";

.header {
    width: 100%;
    background: linear-gradient(0deg, #651FFF -1.81%, #FF5900 33.5%, rgba(253, 191, 15, 0.3) 84.16%, rgba(253, 192, 18, 0.30875) 84.17%, #FFFFFF 145.56%), #FDBF0F;
    font-family: "Roboto Condensed";
    overflow: hidden;

    &__container {
        position: relative;
        max-width: 1280px;
        height: 720px;
        margin: 0 auto;
    }

    &__navigation {
        position: relative;
        display: flex;
        justify-content: space-between;
        max-width: 73.5625rem;
        margin: 0.875rem auto 0 auto;
        z-index: 999;
        //

    }

    &__logo {
        font-weight: bold;
        text-transform: uppercase;
        font-size: 3rem;
        line-height: 3.5156rem;
    }

    &__menu {
        position: relative;
        z-index: 799;
    }

    &__menu-contacts {
        margin-top: 1.1875rem;
        margin-left: 28.25rem;
        max-width: 1280px;
        // margin-left: calc(0px + (452 - 0) * ((100vw - 500px) / (7680 - 500)));
        // top: 89px;
        // right: ;
        // position: absolute;

    }

    &__white-top {
        position: absolute;
        left: 0;
        top: 0;
        background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 59.03%);
        width: 100%;
        height: 720px;
        z-index: 300;
    }

    &__devushka {
        position: absolute;
        left: 11.625rem;
        bottom: 0;
        z-index: 800;
    }

    &__mishen {
        position: absolute;
        top: 3.25rem;
        left: 47.6875rem;
        z-index: 350;

        @include rotation();
    }

    &__rings {
        position: absolute;
        width: 1280px;
        height: 720px;
        top: 0;
        margin: 0 auto;
        overflow: hidden;
    }
    
    &__ring-big {
        position: absolute;
        width: 2000px;
        height: 2000px;
        left: 2.5rem;
        top: -38.125rem;
        border-radius: 100%;
    
        border: 180px solid rgba(253, 191, 15, 0.3);
        box-sizing: border-box;
    }
    
    &__ring-small {
        position: absolute;
        width: 1300px;
        height: 1300px;
        left: 24.375rem;
        top: -16.25rem;
        border-radius: 100%;
    
        border: 150px solid rgba(253, 191, 15, 0.3);
        box-sizing: border-box;
    }

    &__box {
        position: relative;
        max-width: 507px;
        display: flex;
        flex-direction: column;
        // margin: 93px 0 0 57px;
        margin: 5.8125rem 0 0 3.5625rem;
        z-index: 799;
    }

    &__title {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 3.375rem;
        line-height: 1.875rem;
        text-transform: uppercase;
        margin-left: 10.25rem;
        
        color: var(--secondary-color);
    }

    &__aliexpress {
        width: 454.95px;
    }

    &__text {
        font-family: 'Roboto Condensed';
        font-style: normal;
        font-weight: 400;
        font-size: 1.375rem;
        line-height: 1.625rem;
        
        color: var(--primary-color);

        margin: 1.625rem 0 1.875rem 0;
    }

    &__button {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
        padding: 0;
        width: 256px;

        &:hover > span {
            transform: scale(1.1);
            // font-size: 30px;
            transition: .2s;
        }

        &:active > span {
            color: var(--third-color);
            // @include font-gradient-text();
        }
    }

    &__btn-title {
        font-size: 1.5rem;
    }

    &__nav-toggle {
        display: none;
    }
    
    &__nav-toggle-label {
        // margin-left: 1em;
        height: 100%;
        //border: 1px solid red;
        // display: flex;
        // align-items: center;
        margin: auto 0;
        display: none;
        z-index: 1000;
    
        &:hover {
            cursor: pointer;
        }
    }
    
    &__nav-toggle-label span,
    &__nav-toggle-label span::before,
    &__nav-toggle-label span::after {
        display: block;
        background: rgb(0, 0, 0);
        width: 20px;
        height: 2px;
        border-radius: 2px;
        position: relative;
        transition: .4s ease-in-out;
    }
    
    &__nav-toggle-label span::before,
    &__nav-toggle-label span::after {
        content: "";
        position: absolute;
    
    }
    
    &__nav-toggle-label span::before {
        top: 0.3125rem;
    }
    
    &__nav-toggle-label span::after {
        top: -0.3125rem;
    }
    
    &__nav-toggle:checked ~ .header__nav-toggle-label span {
        background: transparent;
    }
    
    &__nav-toggle:checked ~ .header__nav-toggle-label span::before {
        transform: rotate(45deg);
        top: 0px;
    }
    
    &__nav-toggle:checked ~ .header__nav-toggle-label span::after {
        transform: rotate(-45deg);
        top: 0px;
    }


    //Модалка

    &__modal {
        position: fixed;
        font-family: Arial, Helvetica, sans-serif;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0,0,0,0.8);
        z-index: 99999;
        transition: opacity 400ms ease-in;
        display: none;
        pointer-events: none;

        overflow: hidden;
        overflow-y: hidden;

        &:target {
            display: block;
            pointer-events: auto;


            
        }

        body {
            overflow: hidden;
            overflow-y: hidden;

            
            
        }
    }

    &__modal-container {
        max-width: 400px;
        position: relative;
        margin: 300px auto;
        padding: 5px 20px 13px 20px;
        border-radius: 3px;
        background: #fff;
        
    }

    &__modal-close {
        background: #606061;
        color: #FFFFFF;
        line-height: 25px;
        position: absolute;
        right: -12px;
        text-align: center;
        top: -10px;
        width: 24px;
        text-decoration: none;
        font-weight: bold;
        border-radius: 12px;
    }
}

.list {
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;

    &__item {
    }

    &__content {
        display: flex;
        align-items: flex-start;
    }

    &__box-address {
        width: 224px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__address {
        font-size: 0.875rem;
    }

    &__box-contacts {
        width: 230px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__box-links {
        width: 194px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__number {
        font-size: 1.5rem;
        font-weight: bold;
        text-decoration: none;
        color: var(--primary-color);
    }

    &__email {
        margin: 0.5rem 0 0 0;
        text-decoration: none;
        color: var(--primary-color);
        font-size: 0.875rem;
    }

    &__button {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
        padding: 0;
        width: 178px;

        &:hover > span {
            transform: scale(1.1);
            // font-size: 30px;
            transition: .2s;
        }

        &:active > span {
            color: var(--third-color);
            // @include font-gradient-text();
        }
    }

    &__btn-img {
        width: 100%;
    }

    &__btn-title {
        position: absolute;
        font-weight: bold;
        color: var(--secondary-color);
    }

    &__link {
        font-size: 1rem;
        padding: 0.625rem;
        font-weight: bold;
        text-decoration: none;
        color: var(--primary-color);
        transition: color 0.3s;

        &:hover {
            color: var(--icon-second-color);
        }
    }
}

.list-navigation {
    width: 844px;
    margin-top: 0.625rem;
}

.list-contacts {
    max-width: 764px;
}

.icon-cursore,
.icon-speeker {
    font-size: 1.1875rem;
}

.position-speeker {
    margin: 0.25rem 0 0 0;
}






