.section-second {
    background-color: var(--bg-color);
    overflow: hidden;

    &__container {
        position: relative;
        min-height: 716px;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }

    &__title {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 2rem;
        color: var(--secondary-color);
        margin-top: 4.375rem;
        margin-bottom: 4.8125rem;
        max-width: 461px
    }

    &__list {

    }

    &__item {
    }

    &__btn {
        margin-top: 0.9375rem;
        width: 316px;
    }

    &__btn-title {
        font-family: 'Roboto Condensed';
        font-style: normal;
        font-weight: 700;
        font-size: 1.5rem;
    }

    &__hands {
        position: absolute;
        bottom: 0;
        left: 37.875rem;
    }
}


.list {
    flex-wrap: wrap;

    &__item {
        // width: 374px;
        // height: 120px;
        // background-color: var(--secondary-color);
    }

    &__section-second {
        width: 374px;
        height: 120px;
        background-color: var(--secondary-color);
        margin-bottom: 2.9375rem;
        display: flex;
        justify-content: space-around;
        align-items: center;
        position: relative;
        overflow: hidden;

        &::before {
            content: "";
            position: absolute;
            width: 374px;
            height: 120px;
            left: -374px;
            transition: left .5s ease-in-out;
            background-color: rgb(48, 48, 48);
        }

        &:hover {
            &::before {
                content: "";
                position: absolute;
                width: 374px;
                height: 120px;
                left: 0;
                background-color: rgb(48, 48, 48);
            }
        }
    }

    &__title-sectrion-second{
        max-width: 252px;
    }
}

.list-sec {
    max-width: 770px;
}

.icon-section-second {
    font-size: 2.3262rem;
    color: var(--icon-color);
}

.section-second__title,
.list-sec {
    position: relative;
    z-index: 500;
}
