.section-fourth {
    background-color: var(--bg-two-color);
    overflow: hidden;

    &__container {
        border-top: 1px solid transparent;
        // min-height: 765px;
        height: auto;
        max-width: 1280px;
        position: relative;
        display: block;
    }

    &__title {
        margin: 4.375rem 0 0 3.3125rem;
    }

    &__box-absolute {
        position: absolute;
        bottom: 0;
        width: 100%;
    }

    &__box {
        position: relative;
        margin: 3.125rem auto 0 auto;
        width: 520px;
        height: 652px;
        display: flex;
        /* justify-content: flex-end; */
        align-items: flex-end;
        // width: calc(260px + (520 - 260) * ((100vw + 2500px) / (7680 - 3500)));
        // width: 40.625%;
        // max-width: 520px;
        // height: calc(175px + (652 - 175) * ((100vw - 500px) / (1920 - 500)));
        // max-height: 76%;
        // max-width: 100%;
        // max-width: 520px;
        // max-height: 582px;
    }

    &__img {
        // position: absolute;
        // width: 520px;
        width: 100%;
        // width: calc(157px + (520 - 200) * ((100vw - 500px) / (1920 - 500)));

        // bottom: 0;
        // left: 50%;
        // margin-left: -217px;
    }

    &__circle {
        position: relative;
        box-sizing: border-box;
        min-width: 62px;
        min-height: 62px;
        // width: 11.92307692307692%;
        // height: 10.65292096219931%;

        // width: calc(31px + (62 - 31) * ((100vw + 2500px) / (7680 - 3500)));
        // height: calc(31px + (62 - 31) * ((100vw + 2500px) / (7680 - 3500)));
        // border: calc(3.5px + (7 - 3.5) * ((100vw + 2500px) / (7680 - 3500))) solid var(--icon-second-color);

        border: 7px solid var(--icon-second-color);
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        &::before {
            content: "";
            min-width: 40px;
            min-height: 40px;
            // width: calc(20px + (40 - 20) * ((100vw + 2500px) / (7680 - 3500)));
            // height: calc(20px + (40 - 20) * ((100vw + 2500px) / (7680 - 3500)));
            background-color: var(--bg-color);
            border-radius: 100%;
        }
    }

    &__circle-num {
        position: absolute;
        font-family: 'Roboto Condensed';
        font-style: normal;
        font-weight: 700;
        font-size: 1.5rem;
        // font-size: calc(12px + (24 - 12) * ((100vw + 2500px) / (7680 - 3500)));
        line-height: 1.75rem;
        // line-height: calc(14px + (28 - 14) * ((100vw + 2500px) / (7680 - 3500)));
        color: var(--secondary-color);
    }

    &__circle-all {
        position: absolute;
        width: 302px;
        // width: calc(151px + (302 - 151) * ((100vw + 2500px) / (7680 - 3500)));
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__circle-1 {
        flex-direction: row-reverse;
        left: -14.4375rem;
        top: 26.125rem;
        // left: -44.884615384615%;
        // left: calc(-149px + (-249 + 149) * ((100vw - 500px) / (1920 - 500)));
        // top: calc(
        //     48vh - (652px + 399px)
        // // );
        // top: 61.196319018404%;

        // top: calc(
        //     48vh - (172px + 24px)
        //   );
        // left: -45%;
        // top: 64%;
        // margin-left: -151px;
        text-align: right;
    }

    &__circle-2 {
        flex-direction: row-reverse;
        left: -14.75rem;
        top: 9.1875rem;   
        // left: -43%;
        // top: 20%;
        text-align: right;
    }

    &__circle-3 {
        left: 20.625rem;
        top: 2.9375rem;
        // left: 63%;
        // top: 7%;
    }

    &__circle-4 {
        // left: 445px;
        // top: 275px;
        left: 30.625rem;
        top: 17rem;
        // left: 94%;
        // top: 39%;
    }

    &__text {
        max-width: 226px;
        // width: calc(113px + (226 - 113) * ((100vw + 2500px) / (7680 - 3500)));
        // font-size: calc(8px + (16 - 8) * ((100vw + 2500px) / (7680 - 3500)));
    }
}
