.footer {
    font-family: "Roboto Condensed";
    overflow: hidden;

    &__container {
        max-width: 1280px;
        margin: 3.5625rem auto;
    }

    &__menu-contacts {
        margin-top: 8.5625rem;
        margin-left: 0px;
    }

    &__facebook {
        display: flex;
        justify-content: center;
        align-items: center;
        
    }
    
    &__box-contacts-facebook {
        width: auto;
    }
}

.list-contacts-footer {
    max-width: 1177px;
    margin: 0 auto;

    .list {
        &__item {
            margin: 0 0 1.875rem 0;
        }
    }
}

.icon-facebook {
    font-size: 1.875rem;
    text-align: center;
}

.list-footer-navigation {
    width: 844px;
    margin-top: 0.625rem;
}