.section-first {
    width: 100%;
    font-family: 'Roboto', sans-serif;
    overflow: hidden;

    &__container {
        max-width: 1280px;
        min-height: 650px;
        margin: 0 auto;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
    }

    &__situation {
        max-width: 486px;
        position: relative;
    }

    &__img-girls {
    }

    &__title {
        max-width: 396px;
        margin-top: 3.375rem;
        margin-left: 0.625rem;
        font-size: 1.125rem;
        font-weight: 400;
        line-height: 1.75rem;

        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
    }

    &__check {
        position: absolute;
        left: -5.4375rem;
        top: 1.3125rem;    
        width: 131px;
        height: 40px;
        background-image: url("../images/check.svg");
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__check-two {
        top: -1.625rem;
        left: 0.0625rem;
    }

    &__check-title {
        color: var(--secondary-color);
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 1.125rem;
        line-height: 1.3125rem;

        &::first-letter {
            text-transform: uppercase;
        }
    }

    &__list {
        position: relative;
    }

    &__item {
        max-width: 470px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &:first-child {
            margin-top: 4rem;
        }

        &:not(:first-child) {
            margin-top: 5rem;
        }
    }

    &__circle {
        position: relative;
        box-sizing: border-box;
        width: 120px;
        height: 120px;
        border: 24px solid var(--third-color);
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 2.75rem;

        &::before {
            content: "";
            width: 41px;
            height: 41px;
            background-color: var(--third-color);
            border-radius: 100%;
        }
    }

    &__circle-num {
        position: absolute;
        color: var(--secondary-color);
        background-color: var(--primary-color);
        width: 33px;
        height: 33px;
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        left: -1.5rem;
        top: 0px;

        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 1.125rem;
    }

    &__number {
        position: absolute;
        font-family: 'Roboto Condensed';
        font-style: normal;
        font-weight: 700;
        font-size: 9rem;
        line-height: 10.5625rem;

        left: 1.875rem;
        top: -3.3125rem;
        
        color: var(--primary-color);
    }

    &__box {
        max-width: 303px;
        margin: 0;

        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 1.25rem;
    }
    
    &__anotation {
        position: absolute;
        left: 10.5rem;
        top: 2.125rem;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 500;
        font-size: 1rem;
    }

