.cccc {
    box-sizing: border-box;
    width: 120px;
    height: 120px;
    border: 24px solid var(--third-color);
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cccc::before {
    content: "";
    width: 41px;
    height: 41px;
    background-color: var(--third-color);
    border-radius: 100%;
}
