.start {
    position: relative;
    display: flex;
    max-width: 1920px;
    min-height: 300px;
    /* border:1px solid red; */
    margin: 0 auto;
    background: var(--icon-color);

    &__container {
        max-width: 1170px;
        margin: 0 auto;
    }

    &__main-text {
        display: flex;
        max-width: 1162px;
        height: 63px;
        margin: 3.0625rem 0 0 0;
        /* border: 1px solid red; */
    }

    &__main-txt {
        margin: 0 0 0 0;
        font-family: 'Roboto';
        font-weight: bold;
        font-size: 2rem;
        line-height: 2.625rem;
        color: #FFFFFF;
        margin: 3.0625rem 0 0 0;
    }

    &__main-box {
        display: flex;
        max-width: 1267px;
        min-height: 61px;
        flex-wrap: wrap;
        margin: 2.75rem 0 0 0;
        justify-content: center;
    }

    &__item {
        display: flex;
        // width: 260px;
        min-height: 61px;
        align-items: center;
        /* border: 1px solid green; */
    }

    &__main-block-txt {
        font-family: 'Roboto';
        font-size: 1rem;
        line-height: 1.1875rem;
        display: flex;
        align-items: center;
        color: var(--secondary-color);
        margin: 0.625rem 0 0 1.2rem;
    }
}
.right-arrow {
    display: inline-flex;
    position: relative;
    width: 50px;
    height: 20px;
    cursor: pointer;
    margin: 1.25rem;

    &::after,
    &::before {
        display: inline-flex;
        position: absolute;
        content: '';
    }

    &::before {
        top: 0.5rem;
        left: 0;
        width: 50px;
        height: 2px;
        background-color: var(--secondary-color); 
    }

    &::after {
        width: 10px;
        height: 10px;
        top: 0.125rem;
    }

    &::after {
        right: 0;
        border-top: 3px solid var(--secondary-color);
        border-right: 3px solid var(--secondary-color);
        transform: rotate(45deg);
    }
}
