.section-fifth {
    overflow: hidden;

    &__container {
        max-width: 1170px;
        min-height: 720px;
        margin: 0 auto;
    }

    &__title {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 2rem;
        line-height: 2.625rem;
        margin: 4.4375rem 0 0 0;
    }

    &__box {
        display: flex;
        margin: 7.6875rem 0 0 0;
        position: relative;

        background: #FFFFFF;
        border: 1px solid rgba(229, 229, 229, 0.6);
        box-sizing: border-box;
        box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.14), 0px 16px 24px rgba(0, 0, 0, 0.08);


        //*********** Для SLider
        // position: absolute;
        // width: 100%;
        // transform: translateX(-100%);
        // transition-duration: .4s;
        // left: 0;
        // top: 0;
        // opacity: 1;
    }

    &__box-logo {
        width: 300px;
        display: flex;
        justify-content: center;
        align-items: center;

    }

    &__check {
        position: absolute;
        left: 2.125rem;
        top: -1.25rem;
    }

    &__paket {
        position: absolute;
        top: -16rem;
        left: 43.5625rem;
        z-index: -1;
    }

    &__dot-group {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 72px;
        margin: 3rem auto 2.6875rem auto;
    }

    &__dot-item {
        width: 8px;
        height: 8px;
        background-color: var(--primary-color);
        border-radius: 100%;

        //************* Для SLider
        // opacity: 0;
        // position: absolute;
        // cursor: pointer;
    }

    &__dot-item_active {
        width: 12px;
        height: 12px;
        background-color: var(--icon-second-color);
        border-radius: 100%;
    }

    // &__slider {
    //     position: relative;
    //     overflow: hidden;
    //     height: 540px;
    //     width: 1170px;
    // }
}

