.section-fifth .content {
    width: 100%;
    margin: 0;

    &__title {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 2.25rem;
        line-height: 2.625rem;
        max-width: 800px;
        margin: 3.4375rem 0 0 0;
    }

    &__text {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 1rem;
        line-height: 1.5rem;
        max-width: 700px;
    }
}

.statistic {
    display: flex;
    flex-wrap: wrap;
    max-width: 844px;
    justify-content: space-between;
    margin: 4.1875rem 0 3rem 0;

    &__box {
        display: flex;
        align-items: center;
        max-width: 422px;
        justify-content: space-between;
    }

    &__nums {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 3rem;
        line-height: 3.5rem;
        margin: 0 1.0625rem 0 1.25rem;
    }

    &__title {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 500;
        font-size: 1rem;
        line-height: 1.1875rem;
        max-width: 112px;
    }

    &__pulsing {
        @include pulsing();
    }
}
